import { Antd, RequiredAsterisk } from "components";
import { ChangeEventHandler } from "react";

type Props = {
  subtitle: string;
  setSubtitle: ChangeEventHandler;
};

export function SubtitleInput({ setSubtitle, subtitle }: Props) {
  return (
    <Antd.Input.Group>
       
      <RequiredAsterisk />
      <Antd.Input
        value={subtitle}
        onChange={setSubtitle}
        placeholder="  "
      />
    </Antd.Input.Group>
  );
}
